'use client' 
// Bắt buộc với Next.js App Router để component này chạy ở client-side.
// Nếu bỏ dòng này, hook như useUser() sẽ báo lỗi vì chạy server-side.

import Image from 'next/image'; 
// Import component Image của Next.js
// Hỗ trợ resize, lazy load, tối ưu ảnh tự động

import { Button } from "@/components/components/ui/button" 
// Import Button component từ ShadCN UI
// Sử dụng để tạo các nút với style sẵn

import { useUser, UserButton } from "@clerk/nextjs"
// useUser: hook để lấy thông tin user (đăng nhập/đăng xuất)
// UserButton: component hiển thị avatar + menu đăng xuất

import Link from 'next/link';

export default function Header() {
    const { user, isSignedIn } = useUser(); 
    // Lấy thông tin user hiện tại
    // isSignedIn = true nếu user đã đăng nhập

    return (
        <div className='p-5 flex flex-row justify-between items-center border-b-2 border-gray-300'>

            {/* Logo + Name */}
            <div className="flex items-center">
                <Image src={"/LogoWeb-removebg-preview.png"} alt="Logo" width={50} height={50} />
                
                <span className="ml-2 font-bold text-lg text-green-400">Myapp</span>
                {/* Tên app */}
                {/* ml-2: margin-left 0.5rem */}
                {/* font-bold: chữ in đậm */}
                {/* text-lg: font-size lớn */}
            </div>

            {/* Buttons */}
            <div className="flex gap-2">

                {isSignedIn ? (
                    <UserButton />
                    /* Nếu user đã đăng nhập → hiện avatar + menu logout của Clerk */
                ) : (
                    <div className='flex gap-3 items-center'>

                    <Link href="/dashboard/auth/sign-in">
                        {/* variant="outline": nút viền */}
                        {/* rounded-full: bo tròn hoàn toàn */}
                        <Button variant="outline" className='rounded-full cursor-pointer'>Sign In</Button>
                    </Link>

                    <Link href="/dashboard/auth/sign-in">
                        <Button className='rounded-full cursor-pointer'>Get Started</Button>
                    </Link>
                    </div>
                )}
            </div>

        </div>
    )
}
